.profile {
  max-width: 270px;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 2px 2px 14px -6px rgba(75, 75, 78, 1);
  overflow: hidden;
}

.description {
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.avatar {
  width: 100px;
  height: auto;
  border-radius: 50%;
  background-color: rgb(195, 225, 215);
}

.name {
  color: rgba(33, 31, 31, 0.839);
  font-weight: 700;
  font-size: 16px;
  margin-top: 40px;
}

.tag {
  font-size: 14px;
  font-weight: 300;
  margin-top: 10px;
  color: rgb(101, 101, 101);
}

.location {
  font-size: 14px;
  font-weight: 300;
  margin-top: 10px;
  color: rgb(101, 101, 101);
}

.stats {
  display: flex;
  width: 100%;
  justify-content: space-around;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0;
  border-top: 1px solid #ccc;
  background-color: rgb(243, 246, 249);
  flex-basis: calc(270px / 3);
}

.item:not(:last-child) {
  border-right: 1px solid #ccc;
}

.label {
  color: rgb(101, 101, 101);

  font-weight: 300;
  font-size: 12px;
}

.quantity {
  color: rgba(33, 31, 31, 0.839);
  font-weight: 700;
  margin-top: 5px;
}
